import Router from 'vue-router'
import Login from '@/components/Login'
import TabPage from '@/components/TabPage'
import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import BrowseData from '@/components/BrowseData'
import QueryType from '@/components/QueryType'
import UnRecognizePage from '@/components/UnRecognizePage'
import AnnoQueryPage from '@/components/AnnoQueryPage'
import TypeManagePage from '@/components/TypeManagePage'
import KnowledgePage from '@/components/KnowledgePage'
import ModalTerm from '@/components/ModalTerm'
import TermPage from '@/components/TermPage'
import AtomicTermPage from '@/components/AtomicTermPage'
import ConceptPage from '@/components/ConceptPage'
import DrawPage from '@/components/DrawPage'
export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'',
      component:Container,
      leaf:true,
      children:[
        {path:'/knowledgePage',component:KnowledgePage,name:''},
      ]
    },
    {
      path:'/',
      name:'',
      component:Container,
      children:[
        {path:'/annoPage',component:AnnoPage,name:''},
        {path:'/unRecognizePage',component:UnRecognizePage,name:''},
        {path:'/annoQueryPage',component:AnnoQueryPage,name:''},
        {path:'/drawPage',component:DrawPage,name:''}
      ]
    },
    {
      path:'/',
      name:'',
      component:Container,
      children:[
        {path:'/typeManagePage',component:TypeManagePage,name:''},
        // {path:'/browseData',component:BrowseData,name:''},
        // {path:'/queryType',component:QueryType,name:''},
        {path:'/termPage',component:TermPage,name:''},
        {path:'/atomicTermPage',component:AtomicTermPage,name:''},
        {path:'/conceptPage',component:ConceptPage,name:''}
      ]
    },
    {
      path:'/login',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'/',
      name:'excel',
      component:Container,
      leaf:true,
      children:[
        {path:'/excel',component:ModalTerm,name:'excel'},
      ]
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/login'}
    }
  ]
})



